import tuiIcon from "@/components/icon/icon"
import tuiDrawer from "@/components/drawer/drawer"
import tuiLoadmore from "@/components/loadmore/loadmore"
import tuiNomore from "@/components/nomore/nomore"
import tuiTopDropdown from "@/components/top-dropdown/top-dropdown"
export default {
	components: {
		tuiIcon,
		tuiDrawer,
		tuiLoadmore,
		tuiNomore,
		tuiTopDropdown
	},
	data() {
		return {
			getimg: this.$getimg,
			currentId: 0,
			current_children_id: 0,
			currentName: '',
			flist: [],
			slist: [],
			category: '',
			searchKey: "", //搜索关键词
			width: 200, //header宽度
			height: 64, //header高度
			inputTop: 0, //搜索框距离顶部距离
			arrowTop: 0, //箭头距离顶部距离
			dropScreenH: 0, //下拉筛选框距顶部距离
			attrData: [],
			attrIndex: -1,
			dropScreenShow: false,
			scrollTop: 0,
			tabIndex: 0, //顶部筛选索引
			isList: false, //是否以列表展示  | 列表或大图
			drawer: false,
			drawerH: 0, //抽屉内部scrollview高度
			selectedName: "综合",
			selectH: 0,
			dropdownList: [{
				name: "综合",
				selected: true
			}, {
				name: "价格升序",
				selected: false,
			}, {
				name: "价格降序",
				selected: false,
			}],
			attrArr: [{
				name: "新品",
				selectedName: "新品",
				isActive: false,
				list: []
			}],
			productList: [],
			pageIndex: 1,
			loadding: false,
			pullUpOn: true,
			cid: '',
			sid: '',
			ssid:'',
			is_search :0

		}
	},
	onLoad: function(options) {
		if (options.cid) {
			this.cid = options.cid
			this.currentId=options.cid
			if (!options.sid || options.sid==0) {
				if (this.cid == 0) {
					console.log('请求所有商品')
					this.get_pro()
				} else {
					console.log('请求大类商品')
					this.get_pro_cate(this.cid)
				}
			} else {
				this.sid = options.sid
				this.current_children_id=options.sid
				console.log('有sid',this.sid)
				this.get_pro_cate(this.sid)
			}
		}
		if(options.key){
			this.is_search = 1
			this.get_pro_search(options.key)
		}


		this._load()
		if(this.is_search == 0){
			this.closeDrawer()
		}
		
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();







		uni.getSystemInfo({
			success: (res) => {
				this.width = obj.left || res.windowWidth;
				this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
				this.inputTop = obj.top ? (obj.top + (obj.height - 30) / 2) : (res.statusBarHeight + 7);
				this.arrowTop = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
				this.searchKey = options.searchKey || "";
				//略小，避免误差带来的影响
				this.dropScreenH = this.height * 750 / res.windowWidth + 186;
				this.drawerH = res.windowHeight - uni.upx2px(100) - this.height
			}
		})
	},
	methods: {
		_load() {
			this.tabbar = this.$api.http.get('category/all_category').then(res => {
				let list = res.data
				list.forEach(item => {
					if (!item.pid) {
						this.flist.push(item); //pid为父级id, 没有pid或者pid=0是一级分类
					} else {
						this.slist.push(item); //2级分类
					}
				})
			})
		},
		get_pro() { 
			this.$api.http.get('product/get_recent').then(res => { //所有商品
				this.productList = res.data
				this.all = res.data
				console.log(this.productList)
			})
		},
		get_pro_cate(id) {
			this.$api.http.get('product/get_cate_pros?id=' + id).then(res => {
				this.productList = res.data
				this.all = res.data
				console.log(this.productList)
			})
		},
		get_pro_search(key){
			this.$api.http.get('product/search?name='+key).then(res => {
				this.productList = res.data
				this.all = res.data
				console.log(this.productList)
				this.is_search = 0
			})
		},
		//一级分类点击
		tabtap(item) {
			this.currentName = item.category_name;
			this.currentId = item.category_id;
			this.current_children_id = 0
		},
		//点击2级分类
		tab_children(id) {
			if (this.current_children_id == id) {
				this.current_children_id = 0
			} else {
				this.current_children_id = id
			}
		},
		//点击一级分类-全部
		set_all() {
			this.currentId = 0
			this.current_children_id = 0
		},
		px(num) {
			return uni.upx2px(num) + "px"
		},
		btnDropChange: function(e) {
			let index = e.currentTarget.dataset.index;
			let arr = JSON.parse(JSON.stringify(this.attrArr[index].list));
			if (arr.length === 0) {
				this.$set(this.attrArr[index], "isActive", !this.attrArr[index].isActive)
			} else {
				this.attrData = arr;
				this.attrIndex = index;
				this.dropScreenShow = true;
				this.$set(this.attrArr[index], "isActive", false);
				this.scrollTop = 1;
				this.$nextTick(() => {
					this.scrollTop = 0
				});
			}
		},
		btnSelected: function(e) {
			let index = e.currentTarget.dataset.index;
			this.$set(this.attrData[index], "selected", !this.attrData[index].selected)
		},
		reset() {
			let arr = this.attrData;
			for (let item of arr) {
				item.selected = false;
			}
			this.attrData = arr
		},
		btnCloseDrop() {
			this.scrollTop = 1;
			this.$nextTick(() => {
				this.scrollTop = 0
			});
			this.dropScreenShow = false;
			this.attrIndex = -1
		},
		btnSure: function() {
			let index = this.attrIndex;
			let arr = this.attrData;
			let active = false;
			let attrName = "";
			//这里只是为了展示选中效果,并非实际场景
			for (let item of arr) {
				if (item.selected) {
					active = true;
					attrName += attrName ? ";" + item.name : item.name
				}
			}
			let obj = this.attrArr[index];
			this.btnCloseDrop();
			this.$set(obj, "isActive", active);
			this.$set(obj, "selectedName", attrName);
		},
		showDropdownList: function() {
			this.selectH = 246;
			this.tabIndex = 0;
		},
		hideDropdownList: function() {
			this.selectH = 0
		},
		dropdownItem: function(e) {
			let index = e.currentTarget.dataset.index;
			let arr = this.dropdownList;
			for (let i = 0; i < arr.length; i++) {
				if (i === index) {
					arr[i].selected = true;
				} else {
					arr[i].selected = false;
				}
			}
			this.dropdownList = arr;
			this.selectedName = index == 0 ? '综合' : '价格';
			this.selectH = 0
		},
		screen: function(e) {
			let index = e.currentTarget.dataset.index;
			if (index == 0) {
				this.showDropdownList();
			} else if (index == 1) {
				this.tabIndex = 1
			} else if (index == 2) {
				this.isList = !this.isList
			} else if (index == 3) {
				this.drawer = true
			}
		},
		reset_choose() {
			this.currentId = 0
			this.current_children_id = 0
		},
		closeDrawer() {
			let sid = 0
			if(this.sid){
				sid = this.sid
			}
			if (this.currentId > 0) {
				if (this.current_children_id > 0) {
					sid = this.current_children_id
				} else {
					sid = this.currentId
				}
			}
			console.log('sid:', sid)
			if (sid && sid != 0) {
					console.log('pp7')
					this.get_pro_cate(sid)
				//请求-指定分类的商品
			} else {					
				if(this.cid){	
					console.log('cid:',this.cid)
					this.get_pro_cate(this.cid)
				}else{
					this.get_pro()
				}
			}
			//还有搜索部分
			this.drawer = false
			this.sid = ''
		},
		back: function() {
			if (this.drawer) {
				this.closeDrawer()
			} else {
				uni.navigateBack()
			}
		},
		search: function() {
			uni.redirectTo({
				url: '../news-search/news-search'
			})
		},
		detail(id) {
			uni.navigateTo({
				url: '/pages/extend-view/productDetail/productDetail?id=' + id
			})
		}
	},
	onPullDownRefresh: function() {
		let loadData = JSON.parse(JSON.stringify(this.productList));
		loadData = loadData.splice(0, 10);
		this.productList = loadData;
		this.pageIndex = 1;
		this.pullUpOn = true;
		this.loadding = false;
		uni.stopPullDownRefresh()
	},
	onReachBottom: function() {
		if (!this.pullUpOn) return;
		this.loadding = true;
		if (this.pageIndex == 4) {
			this.loadding = false;
			this.pullUpOn = false
		} else {
			let loadData = JSON.parse(JSON.stringify(this.productList));
			loadData = loadData.splice(0, 10);
			if (this.pageIndex == 1) {
				loadData = loadData.reverse();
			}
			this.productList = this.productList.concat(loadData);
			this.pageIndex = this.pageIndex + 1;
			this.loadding = false
		}
	}
}
